<template>
  <div>

    <group title="Default">
      <x-input title="message" placeholder="I'm placeholder"></x-input>
    </group>

    <group title="">
      <x-input title="message" placeholder="I'm placeholder" :show-clear=false></x-input>
    </group>

    <group title="set is-type=china_name">
      <x-input title="" placeholder="" is-type="china_name"></x-input>
    </group>

    <group title="set keyboard=number and is-type=china_mobile">
      <x-input title="" placeholder="" keyboard="number" is-type="china_mobile"></x-input>
    </group>

    <group title="set is-type=email">
      <x-input title="" placeholder="" is-type="email"></x-input>
    </group>

    <group title="set is-type=url">
      <x-input title="" placeholder="URL" is-type="url"></x-input>
    </group>

    <group title="set is-type=ip and required=false">
      <x-input title="ip" placeholder="ip" is-type="ip" :required=false></x-input>
    </group>

    <group title="set min=2 and max=5">
      <x-input title="2-5" placeholder="" :min=2 :max=5></x-input>
    </group>

    <group title="">
      <x-input title="6" type="text" placeholder="" :value.sync="password" :min=6 :max=6 @change="change"></x-input>
      <x-input title="6" type="text" placeholder="" :equal-with="password"></x-input>
    </group>

  </div>
</template>

<script>
  import { Input as XInput, Group } from '../components/'
  export default {
    components: {
      XInput,
      Group
    },
    data: function () {
      return {
        password: '123465'
      }
    },
    methods: {
      change: function (val) {
        console.log(val)
      }
    }
  }
</script>


